import { Component, OnInit, Inject } from '@angular/core';
import {MatDialog, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { User } from "./../user/user.model";
import { UserService } from "./../user/user.service";
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-usersearchmodal',
  templateUrl: './usersearchmodal.component.html',
  styleUrls: ['./usersearchmodal.component.css']
})
export class UserSearchModalComponent implements OnInit {
  users: User[] = new Array();
  private usersStub: Subscription;

  constructor(@Inject(MAT_DIALOG_DATA) public data: User[], private userService: UserService,) {}

  ngOnInit() {
     this.userService.getUsers();
    this.usersStub = this.userService.getTaskUpdateListener()
      .subscribe((users: User[]) => {
        this.users = users;
      });
  }

}
